.loader {
  width: 48px;
  height: 48px;
  background: #f2a3a5;
  border-radius: 50%;
  margin: 12px 100px;
  animation: roll 1s ease-in-out infinite alternate;
  position: relative;
}

.loader:after {
  content: "";
  border: 5px solid #ff3d00;
  border-color: #ff3d00 rgba(0, 0, 0, 0);
  border-radius: 50%;
  position: absolute;
  top: 5px;
  bottom: 5px;
  left: 5px;
  right: 5px;
}

@keyframes roll {
  0% {
    transform: translateX(-150%)rotate(0deg);
  }

  100% {
    transform: translateX(150%)rotate(360deg);
  }
}

